import {creatLocalSong} from 'common/js/song'
export function toggleStorage(name,isRemove,value){
	if(typeof isRemove!='boolean'){
		//value
		value=isRemove;
	}else{
		//
		if(isRemove===true){
			//
			localStorage.removeItem(name);
			return;
		}
	}

	if(value!=undefined){
		console.log(value,88)
		localStorage.setItem(name,JSON.stringify(value));
	}else{
		//getLyric
		if(localStorage.getItem(name)==null){
			return null;
		}
		let songArry=[];
		JSON.parse(localStorage.getItem(name)).forEach(item=>{
			songArry.push(creatLocalSong(item));
		})
		
		return songArry;
	}
}

function _normalizeSongs(list){
      let ret=[];
      list.forEach((item)=>{
        let musicData=item;
        if(musicData.id){
          ret.push(createSong(musicData));
        }
      });
      return ret;
    }
export const STORAGE_SONGS_LIST='collectionSongs';//
export const STORAGE_RECORD_LIST='recordSongs';//
export const STORAGE_PLAY_LIST='PlayingSongs';//


